export default {
	state: {
		getToken: true,
		teamObj:{} ,// 30,
		ManyPGMainImage: ''
	},
	mutations: {
		setTeamObj: (state,data) => {
			state.teamObj = data
		},
		setManyPGMainImage: (state,data) => {
			state.ManyPGMainImage = data
		},
		setToken: (state,data) => {
			state.getToken = data
		}
	},
	actions: {
		setTeamObj: ({commit},data) => {
			commit('setTeamObj',data)
		},
		setManyPGMainImage: ({commit},data) => {
			commit('setManyPGMainImage',data)
		}
	},
	getters: {
		teamObj: state => state.teamObj,
		ManyPGMainImage: state => state.ManyPGMainImage
		
	},
	namespaced: true
}