export default {
  state: {
    title: "",
    backKey: false,
    rightTitle: "",
    rightUrl: ''
  },
  mutations: {
    setHeart: (state, data) => {
      state = {
        title: data.title || "",
        backKey: data.backKey || false,
        rightTitle: data.rightTitle || "",
        rightUrl: data.rightUrl || ''
      }
    },
    setTitle: (state, data) => {
      state.title = data.title || ""
    },
    setRightUrl: (state, data) => {
      state.rightUrl = data.rightUrl || ""
    },
    setBackKey: (state, data) => {
      state.backKey = data.backKey || ""
    }
  },
  actions: {

  },
  getters: {

  },
  namespaced: true
}