export default {
  state: {
    list: [],
    gameDate: 0,
    taskToken: '',
    priceAreaId: '',
    price: '',
    reKey: false
  },
  mutations: {
    setList: (state, data) => {
      state.list = data
    },
    setGameDate: (state, data) => {
      state.gameDate = data
    },
    setTaskToken: (state, data) => {
      state.taskToken = data
    },
    setPriceAreaId: (state, data) => {
      state.priceAreaId = data
    },
    setPrice: (state, data) => {
      state.price = data
    },
    setReKey: (state, data) => {
      state.reKey = data
    }
  },
  actions: {
    setList: ({
      commit
    }, data) => {
      commit('setList', data)
    },
    setGameDate: ({
      commit
    }, data) => {
      commit('setGameDate', data)
    },
    setTaskToken: ({
      commit
    }, data) => {
      commit('setTaskToken', data)
    },
    setPriceAreaId: ({
      commit
    }, data) => {
      commit('setPriceAreaId', data)
    },
    setPrice: ({
      commit
    }, data) => {
      commit('setPrice', data)
    },
    setReKey: ({
      commit
    }, data) => {
      commit('setReKey', data)
    }
  },
  getters: {
    list: state => state.list,
    gameDate: state => state.gameDate,
    taskToken: state => state.taskToken,
    priceAreaId: state => state.priceAreaId,
    price: state => state.price,
    reKey: state => state.reKey
  },
  namespaced: true
}