<template>
  <div class="indexBox">
    <!--  -->
    <div class="head_bg">
      <img :src="imgSrc('backgroundImg.png')" mode="widthFix" />
    </div>
    <!--  -->
    <div class="main-box">
      <div class="searchBox">
        <div class="searchInput item">
          <div class="input-box">
            <input type="span" style="font-size: .875rem;color: #9E9E9E;" :adjust-position="true"
              :placeholder="defaultKeyword" @input="inputChange" v-model="searchCommodity" @confirm="doSearch()"
              placeholder-class="placeholder-class" confirm-type="search">
            <div class="search-btn" @click="doSearch(false)"></div>
          </div>
        </div>
        <div @click="toUrl('/QRcode')" hover-class="none">
          <div class="codeBox item">
            <img :src="imgSrc('code.png')" mode="widthFix" />
          </div>
        </div>
      </div>
      <carousel :img-list="imgList" url-key="url" @selected="selectedBanner" />
    </div>
    <!--  -->
    <div class="notice-content">
      <div class="notice-min">
        <img :src="imgSrc('notice.png')" mode="widthFix" />
        <div class="notice-span">
          <span>{{$text.groupText.singleGroupBuying}}</span>
        </div>
      </div>
    </div>
    <!-- <div class="task" @click="toTask" v-show="taskKey">
      <img :src="imgSrc('task.png')" mode="">
    </div> -->
    <!--  -->
    <div class="specialZoneBox">
      <div class="specialZone">
        <div class="bookshelf-content">
          <div v-for="(item, index) in specialZoneList" :key="index">
            <div class="item" @click="specialZone(index,item)">
              <img v-if="item.icon" :src="item.icon" mode="widthFix" />
              <div class="">
                <span :class="specialZoneAv === index ? 'specialZoneAv': ''">{{item.price}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="commodity">
      <div class="commodityHead">
        <img :src="imgSrc('rectangle.png')" mode="widthFix" />
        <span></span>
      </div>
    </div>
    <!--  -->
    <div class="commodityBox" v-for="(item,index) in commodityList" :key="index">
      <div @click="toUrl('/productdetails?pid=' + item.ProductId)">
        <div class="commodityDetails">
          <div class="commFl">
            <img :src="item.MainImage" mode="" />
          </div>
          <div class="commFr">
            <div class="title">
              {{item.Title}}
            </div>
            <div class="deduction">
              <span v-if="item.EnergyTicketNumber > 0"> {{item.EnergyTicketPrice}}</span>
              <span v-if="item.EnergyTicketNumber > 0">{{item.EnergyTicketNumber}}</span>
            </div>
            <div class="price">
              <span> {{item.Price}}</span>
              <span style="span-decoration:line-through">{{item.OldPrice}}</span>
            </div>
            <div class="sold">
              <div class="">
                {{item.SaleNumber}}
              </div>
              <div class="">
                <img v-for="(item,index) in getRandomInt(1,5)" :key="index" style="width: 1.25rem;"
                  src="http://web991-oss.jiujiupingou.com/img/001.png" mode="widthFix" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="commodityBox" v-if="commodityList.length === 0">
      <span></span>
      <!-- <img src="" mode=""/> -->
    </div>
    <div v-show="isLoadMore" v-if="commodityList.length === 0">
      <!-- <uni-load-more :status="loadStatus"></uni-load-more> -->
    </div>
    <!--  -->
    <div class="newPeopleBox" v-if="bulletFrame" @click="bulletFrame = false">
      <img :src="imgSrc('cancel.png')" @click="clickImgCancel" mode="widthFix" />
      <img :src="imgSrc('newpeople.png')" mode="widthFix" />
      <img :src="imgSrc('getinto.png')" @click="getIntoPeople" mode="widthFix" />
    </div>
  </div>
</template>

<script>
  import {
      Toast
  } from 'vant'
  import {
    imgUrl
  } from '../../api/config.js'
  import {
    // setId,
    getPhone
  } from '../../util/token.js'
  import carousel from '../../components/vear-carousel/vear-carousel.vue'
  // import uniLoadMore from '@/components/uni-load-more/uni-load-more'
  import {
    nonce,
    timestamp
  } from '../../util/random.js'
  export default {
    components: {
      carousel,
      // uniLoadMore
    },
    data() {
      return {
        imgList: [],
        bulletFrame: false,
        specialZoneList: [],
        specialZoneAv: 0,
        pageIndex: 1,
        pageSize: 10,
        commodityList: [],
        loadStatus: 'loading', //more-loading-nomore-
        isLoadMore: false, //
        priceAreaId: null,
        searchCommodity: '',
        defaultKeyword: '',
        totalCount: 0
      }
    },
    computed: {
      imgSrc() {
        return (name) => {
          return imgUrl + name
        }
      },
      taskKey() {
        return getPhone() === '18888888888'
      }
    },
    mounted() {
      this.$store.commit('common/setToken', true)
      this.commodityList = []
      this.specialZoneAv = 0
      this.getBanner()
      this.getSpecialZone()
    },
    methods: {
      // async toTask() {
      //   let res = await this.$http.post('/api/v1/UserAuth/GetAuthCert', {}, {
      //     custom: {
      //       nonce: nonce(),
      //       timestamp: timestamp()
      //     }
      //   })
      //   if (res) {
      //     if (res.data.code === 1) {
      //       this.TokenCert = res.data.data.cert
      //       this.userId = res.data.data.UserId
      //       window.location.href =
      //         `http://test-task-web.991ict.com/?UserId=${this.userId}&TokenCert=${encodeURIComponent(this.TokenCert)}`
      //       setId(JSON.stringify(res.data.data.UserId))
      //     }
      //   }
      // },
      getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
      },
      async getBanner() {
        let res = await this.$http.post('/api/v1/setting/getbanner', {}, {
          custom: {
            url: '99',
            nonce: nonce(),
            timestamp: timestamp()
          }
        })
        if (res) {
          let data = res.data
          // console.log(data)
          if (data && data.code === 1) {
            this.imgList = data.data
          }
        }
      },
      async getSpecialZone() {
        let res = await this.$http.post('/api/v1/PriceArea/List', {
          type: 1
        }, {
          custom: {
            url: '99',
            nonce: nonce(),
            timestamp: timestamp()
          }
        })
        if (res) {
          let data = res.data
          if (data && data.code === 1) {
            this.specialZoneList = data.data
            this.getList(this.specialZoneList[0].priceAreaId)
            this.priceAreaId = this.specialZoneList[0].priceAreaId
          }
        }
      },
      async getList(priceAreaId) {
        Toast.loading({
          duration: 0,
          message: '...',
          forbidClick: true,
        });
        let res = await this.$http.post('/api/v1/Product/GetProductList', {
          // pageIndex:this.pageIndex,
          // pageSize:this.pageSize,
          priceAreaId: priceAreaId,
          title: this.searchCommodity
        }, {
          custom: {
            url: '99',
            nonce: nonce(),
            timestamp: timestamp()
          }
        })
        Toast.clear()
        let data = res.data
        if (data.code === 1) {
          this.commodityList = data.data
        }
      },
      specialZone(index, item) {
        if (item.openStatus === 1) {
          this.specialZoneAv = index
          this.pageIndex = 1
          this.commodityList = []
          this.getList(item.priceAreaId)
          this.priceAreaId = item.priceAreaId
        } else {
          this.$message('');
        }
      },
      // 
      inputChange() {

      },
      doSearch() {
        this.pageIndex = 1
        this.commodityList = []
        this.getList(this.priceAreaId)
      },
      selectedBanner() {
        // console.log('', item, index)
      },
      clickImgCancel() {
        this.bulletFrame = false
      },
      getIntoPeople() {
        this.$router.push('/newpeopletask')
      },
      toUrl(url) {
        this.$router.push(url)
      }
    }
  }
</script>

<style lang="less">
  .indexBox {
    height: 100%;
    overflow: auto;

    .head_bg {
      width: 100%;
      height: 10.75rem;

      img {
        width: 100%;
      }
    }

    .lijianImgBox {
      display: flex;
      justify-content: center;

      .lijianImg {
        width: 20.9375rem;

        img {
          width: 100%;
        }
      }
    }

    .main-box {
      margin-top: -8.4375rem;

      .searchBox {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          width: 1.25rem;
          vertical-align: middle;
        }

        .item {
          margin-right: .625rem;
        }

        .gardenImg {
          width: 1.875rem;
          height: 1.875rem;
          border-radius: 100%;
          background-color: #FFD632;
          position: relative;

          img {
            position: absolute;
            top: 50%;
            margin-top: -0.625rem;
            left: 50%;
            margin-left: -0.625rem;
          }
        }

        .searchInput {
          width: 17.1875rem;
          height: 1.875rem;
          line-height: 1.875rem;
          display: flex;
          align-items: center;

          .input-box {
            padding-left: .3125rem;
            width: 100%;
            height: 100%;
            flex-shrink: 1;
            display: flex;
            justify-content: space-around;
            align-items: center;
            background-color: #FFFFFF;
            border-radius: 1.875rem;

            input {
              padding: 0;
              border: unset;
            }
          }

          .search-btn {
            width: 15%;
            margin: 0 0 0 2%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
            font-size: .875rem;
            color: #ff9801;
            border-radius: 1.875rem;
          }

          .placeholder-class {
            color: #9e9e9e;
            font-size: .875rem;

          }
        }
      }
    }

    .notice-content {
      width: 100%;
      height: 1.875rem;
      line-height: 1.875rem;
      display: flex;
      justify-content: center;

      .notice-min {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 1.25rem;
        line-height: 1.25rem;
        width: 20.0625rem;
        background-color: #F0F0F0;
        border-radius: .625rem;
        padding: 0 .625rem;
        text-align: center;
        position: relative;

        img {
          width: 1rem;
          position: absolute;
          top: 50%;
          margin-top: -0.46875rem;
          left: 1.25rem;
        }

        .notice-span {
          span {
            font-size: .625rem;
          }
        }
      }
    }

    .specialZoneBox {
      margin-top: .9375rem;
      display: flex;
      justify-content: center;

      .specialZone {
        width: 21.5625rem;
        height: 4.4375rem;
        padding-top: .75rem;
        box-shadow: 0rem .0625rem .5rem 0rem #E1E1E1;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-radius: .625rem;

        .bookshelf-content {
          white-space: nowrap; // 
          width: 97%;
          height: 100%;
          overflow-x: auto;

          &::-webkit-scrollbar {
            display: none;
            /* Chrome Safari */
          }

          >div {
            display: inline-block;
          }

          .item {
            text-align: center;
            display: inline-block;
            margin: 0 0.75rem;

            img {
              width: 2.25rem;
            }

            span {
              font-size: .875rem;
              color: #000;
            }

            .specialZoneAv {
              color: rgb(248, 143, 26);
            }
          }
        }
      }
    }

    .shopBox {
      display: flex;
      justify-content: center;

      .shopBoxImg {
        width: 24.3125rem;

        img {
          width: 24.3125rem;
        }
      }
    }

    .commodity {
      margin-top: .625rem;
      display: flex;
      justify-content: center;
      align-items: center;

      .commodityHead {
        position: relative;
        width: 20.9375rem;

        img {
          position: absolute;
          left: 0;
          top: .21875rem;
          width: .375rem;
          vertical-align: middle;
          margin-right: .9375rem;
        }

        span {
          font-size: 1.125rem;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #5D5D5D;
          margin-top: .0625rem;
          margin-left: 1.125rem;
          ;
        }
      }

    }

    .commodityBox {
      display: flex;
      justify-content: center;
      margin-top: .5625rem;
      margin-bottom: .625rem;

      .commodityDetails {
        width: 19.4375rem;
        height: 5.875rem;
        background: #FFFFFF;
        box-shadow: 0rem .0625rem .5rem 0rem #E1E1E1;
        border-radius: .625rem;
        padding: .75rem;
        display: flex;

        .commFl {
          float: left;
          width: 5.625rem;
          height: 5.625rem;
          border-radius: .375rem;
          margin-right: .625rem;

          img {
            width: 5.625rem;
            height: 5.625rem;
            border-radius: .375rem;
          }
        }

        .commFr {
          width: 13.1875rem;

          // float: right;
          // margin-right: 1.6875rem;
          .title {
            font-size: .875rem;
            font-weight: 400;
            color: #303030;
            width: 90%;
            display: block;
            white-space: nowrap; //
            text-overflow: ellipsis; //
            overflow: hidden; //
          }

          .deduction {
            margin-top: .375rem;

            span:nth-child(1) {
              font-size: 1rem;
              font-weight: 600;
              color: #FF4200;
              margin-right: .375rem;
            }

            span:nth-child(2) {
              font-size: .625rem;
              font-weight: 400;
              color: #939393;
            }
          }

          .price {
            margin-top: .375rem;

            span:nth-child(1) {
              font-size: 1rem;
              font-weight: 600;
              color: #333333;
              margin-right: .375rem;
            }

            span:nth-child(2) {
              font-size: .625rem;
              font-weight: 400;
              color: #939393;
              ;
            }
          }

          .sold {
            margin-top: .3125rem;
            font-size: .5rem;
            font-weight: 400;
            color: #939393;
            display: flex;
            justify-content: space-between;
          }
        }
      }
    }

    .newPeopleBox {
      z-index: 12000;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      transition: background-color .15s linear;
      background: rgba(38, 38, 38, 0.6);

      img:nth-child(1) {
        position: absolute;
        right: 2.1875rem;
        top: 13.5625rem;
        width: 1.8125rem;
      }

      img:nth-child(2) {
        width: 100%;
        position: absolute;
        top: 16.75rem;
      }

      img:nth-child(3) {
        width: 16.75rem;
        position: absolute;
        top: 28.375rem;
        left: 3.5625rem;
      }
    }
  }
  .task{
    width: 100%;
    height: 6.875rem;
    margin: .3125rem 0;
    img{
      width: 100%;
      height: 100%;
    }
  }
</style>