import axios from 'axios'
import router from '../router'
import qs from 'qs'
import {
  Dialog,
  Toast
} from 'vant';
// import { Toast, MessageBox } from 'element-ui';
import {
  getToken,
  getUserCode,
  // getUser_string,
  removeUserId,
  removeToken,
  removeUserCode,
  removePhone
} from '../util/token.js'
import {
  baseUrl,
  baseUrl1,
} from './config.js'
import SHA1 from 'sha1'

function codeLogout (response) {
  Toast.clear()

  Dialog.confirm({
    title: '',
    message: response.data.msg,
    confirmButtonText: ''
  }).then(() => {
    removeUserId()
    removeToken()
    removeUserCode()
    removePhone()
    router.push('/login')
    // const u = navigator.userAgent;
    // const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    // if (isiOS) {
    //   //vueiOS iOS  toLogin
    //   window.webkit.messageHandlers.toLogin.postMessage("{}");
    // } else {
    //   //vueAndroid Android toLogin
    //   // eslint-disable-next-line
    //   nuandouAndroid.toLogin("{}");
    // }
  }).catch(() => {

  });
}

const http = axios.create({
  // 
  baseURL: ``,
  timeout: 30000
})
let url = ''
// 
http.interceptors.request.use((config) => {

  // if(config.custom.login)
  if (config.custom.loading) {
    Toast.loading({
      duration: 0,
      message: '...',
      forbidClick: true,
    });
  }
  if (config.custom.url === '99') {
    if (Array.isArray(baseUrl1)) {
      if (url == '') {
        if (Math.random() > 0.5) {
          url = baseUrl1[0]
        } else {
          url = baseUrl1[1]
        }
      }
      config.baseURL = url
    } else {
      config.baseURL = baseUrl1
    }
    // config.baseURL = baseUrl1
  } else {
    config.baseURL = baseUrl
  }
  let dataStr = ''
  if (config.data) {
    let newData = {}
    Object.keys(config.data).sort().map(key => {
      newData[key] = config.data[key]
    })
    for (let key in newData) {
      dataStr += newData[key]
    }
  }
  config.headers = {
    'Content-Type': 'application/x-www-form-urlencoded',
    appId: 'androidapp',
    appSecret: 'PG99PG',
    // user_string: getUser_string() || '',
    nonce: config.custom.nonce,
    timestamp: config.custom.timestamp,
    signature: SHA1('androidapp' + 'PG99PG' + config.custom.nonce + config.custom.timestamp + dataStr).toUpperCase(),
    userCode: getUserCode() || '',
    userToken: getToken() || '',
    appVersion: '2003.001'
  }
  config.transformRequest = [(data) => {
    return qs.stringify(data, {
      arrayFormat: 'repeat'
    })
  }]
  return config
}, (error) => {
  // 
  return Promise.reject(error)
})

// 
http.interceptors.response.use((response) => {
  if (response.config.custom.codePay !== '') {
    if (response.data.code === 0) {
      Toast({
        duration: 2000, //  toast
        forbidClick: true,
        message: response.data.msg,
        icon: 'none'
      });
      return response
    }
  }
  if (response.data.code === 1001) {
    codeLogout(response)
    return
  }
  if (response.data.code === 1002) {
    codeLogout(response)
    return
  }
  if (response.data.code === 1003) {
    codeLogout(response)
    return
  }
  if (response.data.code === 1004) {
    Toast(response.data.msg);
    return
  }
  if (response.data.code === 2001) {
    codeLogout(response)
    return
  }
  if (response.data.code === 2003) {
    codeLogout(response)
    return
  }
  if (Number(response.data.code) === 2004) {
    codeLogout(response)
    return
  }
  if (Number(response.data.code) === 3004) {
    codeLogout(response)
    return
  }
  if (Number(response.data.code) === 4001) {
    Toast('');
    router.push({ path: '/user/details', replace: true })
    return
  }
  if (response.data.code === 3001) {
    Dialog.confirm({
      title: '',
      message: '',
      messageAlign: true,
      confirmButtonText: ''
    }).then(() => {
      router.push('/advertisement/advertisement')
    });
  }
  if (response.data.code !== 1) {
    Toast({
      duration: 2000, //  toast
      forbidClick: true,
      message: response.data.msg,
      icon: 'none'
    });
  }

  return response
}, (error) => {
  Toast('');
  //
  if (error.statusCode == 401) {
    localStorage.clear()
    router.push({ path: '/home/index', replace: true })
  }
  return Promise.resolve(error)
})

export default http