<template>
	<swipe class="my-swipe img-container" :lazy-render="true" :show-indicators="false" :width="widthPhone" :autoplay="3000" indicator-color="white" @change="swiperChange">
    <swipe-item v-for="(item, index) in imgList" :key="index" :class="currentIndex == index ? 'swiper-item' : 'swiper-item-side'">
      <div class="img"><img @click="clickImg(item)" :class="currentIndex == index ? 'item-img' : 'item-img-side'" :src="item" :style="dontFirstAnimation ? 'animation: none;' : ''" mode="aspectFill"/></div>
    </swipe-item>
  </swipe>
</template>
<script>
  import { Swipe, SwipeItem } from 'vant';
	export default {
    components:{Swipe, SwipeItem},
		name: "vearCarousel",
		props: {
			imgList: {
				type: Array,
				default() {
					return []
				}
			},
			urlKey: {
				type: String,
				default() {
					return ''
				}
			},
		},
		data() {
			return {
				currentIndex: 0,
				dontFirstAnimation: true,
				interval:3000,
				duration: 200
			}
		},
    computed:{
      widthPhone(){
        return (315*window.innerWidth)/375
      }
    },
		methods: {
			swiperChange(e) {
				this.dontFirstAnimation = false
				this.currentIndex = e
			},
			clickImg(item) {
				this.$emit('selected', item, this.currentIndex)
			}
		}
	}
</script>
<style lang="less" scoped>
	.img-container {
		width: 23.4375rem;
    margin: 20px auto;
    /deep/ .swiper-item-side{
      margin: 10px 0;
    }
    /deep/ .van-swipe-item{
      left: 2.5%;
    }
    .img{
      width: 100%;
      height: 100%;
      text-align: center;
      img{
        width: 95%;
      }
    }
	}

	.item-img {
		width: 19.6875rem;
		height: 9.375rem;
		border-radius: .4375rem;
		animation: to-big .3s;
	}

	.swiper-item {
		width: 19.6875rem;
		height: 9.375rem;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.item-img-side {
		width: 19.6875rem;
		height: 8.125rem;
		border-radius: .4375rem;
		animation: to-mini .3s;
	}

	.swiper-item-side {
		width: 19.6875rem;
		height: 8.125rem;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	@keyframes to-mini
	{
		from {
			height: 9.375rem;
		}
		to {
			height: 8.125rem;
		}
	}
	@keyframes to-big
	{
		from {
			height: 8.125rem;
		}
		to {
			height: 9.375rem;
		}
	}
</style>
