<template>
  <div class="home">
    <div class="center" :style="centerStyle"><router-view/></div>
    <div class="foot" :style="footStyle">
      <div class="item" :class="{'active':'/home/index' === routerUrl}" @click="toNav('/home/index')">
        <img v-if="'/home/index' !== routerUrl" :src="imgSrc('index.png')" alt="" srcset="">
        <img v-else :src="imgSrc('index-a.png')" alt="" srcset="">
        <div></div>
      </div>
      <div class="item" :class="{'active':'/home/newTeam' === routerUrl}" @click="toNav('/home/newTeam')">
        <img v-if="'/home/newTeam' !== routerUrl" :src="imgSrc('team.png')" alt="" srcset="">
        <img v-else :src="imgSrc('team-a.png')" alt="" srcset="">
        <div>30</div>
      </div>
      <div class="item item-center" @click="toTask()">
        <div>
          <div :style="itemCenterStyle">
          </div>
        </div>
      </div>
      <div class="item" :class="{'active':'/home/elife' === routerUrl}" @click="toNav('/home/elife')">
        <img v-if="'/home/elife' !== routerUrl" :src="imgSrc('welfareSociety.png')" alt="" srcset="">
        <img v-else :src="imgSrc('welfareSociety-a.png')" alt="" srcset="">
        <div>e</div>
      </div>
      <div class="item" :class="{'active':'/home/user' === routerUrl}" @click="toNav('/home/user')">
        <img v-if="'/home/user' !== routerUrl" :src="imgSrc('user.png')" alt="" srcset="">
        <img v-else :src="imgSrc('user-a.png')" alt="" srcset="">
        <div></div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    imgUrl,
    taskUrl
  } from '../api/config.js'
  import {
    nonce,
    timestamp
  } from '../util/random.js'
  import {
    setId,
    getPhone
  } from '../util/token.js'
// @ is an alias to /src
export default {
  name: 'Home',
  data(){
    return {
      title: '',
      path: '',
      itemCenterStyle:{
        backgroundImage: 'url('+imgUrl+'tab_ic_rwxt_sel.png)'
      }
    }
  },
  computed:{
    showKey(){
      return getPhone() === '18888888888'
    },
    imgSrc(){
        return (name)=>{
          return imgUrl+name
        }
      },
    centerStyle(){
      let h = 100
      var device_type = navigator.userAgent; //userAgent
      // eslint-disable-next-line
      var md = new MobileDetect(device_type); //mobile-detect
      var os = md.os(); //
      if (os == "iOS") {
        h = 97
      }
      if(this.$route.meta.heartKey){
        return {height: `calc(${h}vh - 94px)`}
      }else{
        return {height: `calc(${h}vh - 50px)`}
      }
    },
    footStyle(){
      let b = 0;
      return {bottom:b+'px'}
    },
    routerUrl(){
      return this.path || this.$router.currentRoute.path
    }
  },
  methods:{
    async toTask() {
        let res = await this.$http.post('/api/v1/UserAuth/GetAuthCert', {}, {
          custom: {
            nonce: nonce(),
            timestamp: timestamp()
          }
        })
        if (res) {
          if (res.data.code === 1) {
            this.TokenCert = res.data.data.cert
            this.userId = res.data.data.UserId
            window.location.href =
              `${taskUrl}?UserId=${this.userId}&TokenCert=${encodeURIComponent(this.TokenCert)}`
            setId(JSON.stringify(res.data.data.UserId))
          }
        }
      },
    toNav(url){
      this.path = url
      this.$router.push(url)
    },
    save(){
      this.title=''
      // eslint-disable-next-line
      nuandouAndroid.onItemClick(JSON.stringify({advId: 2,mchId: 2,name: "",type: 0}));
    },
    pay(){
      this.title=''
      // eslint-disable-next-line
      nuandouAndroid.paymentAlipay("https://payment.5upay.com/receipt/index/5f5300ad4e5e4e639347a28635476ef5");
    }
  }
}
</script>

<style lang="less" scoped>
.center{
  margin-bottom: 50px;
  overflow: auto;
}
.foot{
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 44px;
  padding-top: 5px;
  box-shadow: 0 0 5px 0 #ccc;
  background-color: #fff;
  >div{
    flex: 1;
    text-align: center;
    font-size: 12px;
    color: #ccc;
    img{
      width: 24px;
      height: 24px;
    }
  }
  >div.active{
    color: #ea6c10;
  }
  .item-center{
    >div{
      position: relative;
      width: 80%;
      padding-bottom: 80%;
      margin: -20px auto 0;
      border-radius: 50%;
      background-image: linear-gradient( #FBB663, #FB6463);
      >div{
        position: absolute;
        width: 100%;
        padding-bottom: 100%;
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }
}
</style>
