<template>
  <div id="app">
    <div class="app-top" v-if="topKey">
      <icon name="arrow-left icons" @click="back" v-if="backtKey" />
      <span>{{title}}</span>
      <div class="right" @click="toUrl" v-if="rightKey">{{backtTitle}}</div>
    </div>
    <!-- <keep-alive include="myShop"> -->
      <router-view />
    <!-- </keep-alive> -->
  </div>
</template>

<script>
  import {
    Icon
  } from 'vant';
  export default {
    components: {
      Icon
    },
    computed: {
      backtTitle() {
        return this.$route.meta.rightTitle || ''
      },
      title() {
        return this.$store.state.navTop.title
      },
      topKey() {
        return this.$route.meta.heartKey || false
      },
      rightKey() {
        return this.$route.meta.rightKey || false
      },
      backtKey() {
        return !this.$route.meta.backKey
      }
    },
    watch: {
      $route: {
        handler: function(){
          if (this.$route.meta.title) {
          this.$store.commit('navTop/setTitle', {
            title: this.$route.meta.title
          })
        }
        }
      }
    },
    methods: {
      back() {
        if(this.$route.path === '/cashing'){
          this.$router.go(-2)
        }else if(this.$route.path === '/orderList'){
          this.$router.push({path:'/home/index',replace:true})
        }else{
          this.$router.go(-1)
        }
      },
      toUrl() {
        this.$router.push(this.$store.state.navTop.rightUrl)
      }
    }
  }
</script>

<style lang='less'>
  @import url('http://at.alicdn.com/t/font_2658181_386y27x3min.css');

  body {
    margin: 0;
  }

  #app {
    .app-top {
      position: relative;
      height: 29.5px;
      padding: 7px 0;
      line-height: 30px;
      text-align: center;
    }

    .app-top .icons {
      position: absolute;
      top: 10px;
      left: 10px;
      font-size: 23px;
    }

    .app-top span {
      font-weight: bold;
      font-size: 17px;
    }

    .app-top .right {
      position: absolute;
      top: 6px;
      right: 10px;
      font-size: 12px;
      color: rgb(153, 153, 153);
    }
  }
</style>