export default {
	state: {
		addressIs:{}, // 30
		addressXuanze: false
	},
	mutations: {
		setAddressIs: (state,data) => {
			state.addressIs = data
		},
		setAddressXuanze: (state,data) => {
			state.addressXuanze = data
		},
	},
	actions: {
		setAddressIs: ({commit},data) => {
			commit('setAddressIs',data)
		},
		setAddressXuanze: ({commit},data) => {
			commit('setAddressXuanze',data)
		}
	},
	getters: {
		addressIs: state => state.addressIs,
		addressXuanze: state => state.addressXuanze
	},
	namespaced: true
}