const tokenKey = 'token';
const userIdKey = 'userId';
const userUserCodeKey = 'userCode';
const phoneKey = 'phone';
const user_stringKey = 'user_string';
const is_anchorKey = 'is_anchor';

export function setToken(token){
    return localStorage.setItem(tokenKey,token);
}
export function setId(id){
    return localStorage.setItem(userIdKey,id);
}

export function setUserCode(userCode){
    return localStorage.setItem(userUserCodeKey,userCode);
}

export function setPhone(phone){
    return localStorage.setItem(phoneKey,phone);
}

export function setUser_string(user_string){
    return localStorage.setItem(user_stringKey,user_string);
}

export function setis_anchor(is_anchor){
    return localStorage.setItem(is_anchorKey,is_anchor);
}


export function getToken(){
    return localStorage.getItem(tokenKey);  
}
export function getUserId(){
    return localStorage.getItem(userIdKey);  
}
export function getUserCode(){
    return localStorage.getItem(userUserCodeKey);
}
export function getPhone(){
    return localStorage.getItem(phoneKey);  
}
export function getUser_string(){
    return localStorage.getItem(user_stringKey);  
}
export function getis_anchor(){
    return localStorage.getItem(is_anchorKey);  
}
export function removeUserId(){
    return localStorage.removeItem(userIdKey);
}
export function removeUserCode(){
    return localStorage.removeItem(userUserCodeKey);
}
export function removeToken(){
    return localStorage.removeItem(tokenKey);
}
export function removePhone(){
    return localStorage.removeItem(phoneKey);
}
export function removeUser_string(){
    return localStorage.removeItem(user_stringKey);
}
export function removeis_anchor(){
    return localStorage.removeItem(is_anchorKey);
}